import {defineComponent} from "vue";
import {useMapState} from "@/hooks/store";
import Flex, {Item} from "@/components/Flex";
import styles from './home.module.scss'
import head from './images/head.png'
import {FlexAlignItems} from "@/components/Flex/types";
import {hidePhoneNumber} from "@/utils";
import {useRouter} from "vue-router";

export default defineComponent({
	name: 'HeaderComponent',
	setup() {
		const {
			user
		} = useMapState({
			states: ['user']
		})
		const router = useRouter()
		const userDetail = () => {
			router.push('/user/info')
		}

		return () => {
			const account = user.value || {}
			return <Flex class={styles.header} alignItems={FlexAlignItems.center}>
				<Item class={styles.headerHead} shrink={0} onClick={userDetail}>
					<img src={head} alt={account.real_name} />
				</Item>
				<Item grow={1}>
					<label>{hidePhoneNumber(account.mobile)}</label>
				</Item>
			</Flex>
		}
	}
})