import {defineComponent} from "vue";
import Header from "@/component/Home/Header";
import Main from "@/components/Main";
import Card from "@/components/Card";
import {useMapState} from "@/hooks/store";
import styles from './user.module.scss'
import Container from "@/components/Container";
import Footer from "@/component/Footer";
import {useRouter} from "vue-router";

interface CardSlotProps {
	title: string,
	default: string,
	theme: string,
	titleEnter: boolean,
	account?: {
		wait_capital?: string|number,
		c_deal_loan: string|number
	}
}

export default defineComponent({
	name: 'UserHome',
	setup() {
		const {
			user
		} = useMapState({
			states: ['user']
		})
		const router = useRouter()

		const navigator = () => <Header />
		const footer = () => <Footer />

		return () => {
			const account = user.value
			const slots = {
				default: () => <Main>
					{
						[
							{account, title: '债权信息', default: '抱歉，您当前暂未持有债权。', titleEnter: true, theme: styles.userDanger},
							{title: '催收情况', default: '抱歉，您当前暂无催收进展。', titleEnter: false, theme: styles.userWarning},
							{title: '回款情况', default: '抱歉，您当前暂无回款进展。', titleEnter: false, theme: styles.userPrimary},
						].map((item: CardSlotProps) => <Card titleEnter={item.titleEnter} key={item.title} class={styles.userCardItem}
							item={item}
							onClick={() => {
								if (item.titleEnter) {
									router.push('/claims')
								}
							}}
							v-slots={{
								title: (props: CardSlotProps) => <div class={item.theme}>
									{props.title}
								</div>,
								default: ({account,default:defaultContent}: CardSlotProps) => <div class={styles.userClaims}>
									{
										parseInt(`${account?.c_deal_loan}`, 10) > 0 ? <>
											<p>债权金额： {account?.wait_capital}</p>
											<p>债权数量：{account?.c_deal_loan}</p>
										</> : defaultContent
									}
								</div>
							}} />)
					}
				</Main>,
				navigator,
				footer
			}
			return <Container v-slots={slots} />
		}
	}
})