import {defineComponent} from "vue";
import Container from "@/components/Container";
import Navigation from "@/components/Navigation";
import Footer from "@/component/Footer";
import Main from "@/components/Main";
import UserDetail from "@/component/User/Detail";
import Button from "@/components/Button";
import {ButtonTypes} from "@/components/Button/types";
import styles from './user.module.scss'
import {useMapActions} from "@/hooks/store";
import {useRouter} from "vue-router";

export default defineComponent({
	name: 'UserInfo',
	setup() {
		const {logout} = useMapActions({actions: ['logout']})
		const router = useRouter()
		const signOut = () => {
			logout()
			router.push('/')
		}
		const slots = {
			navigator: () => <Navigation title={"个人信息"} />,
			default: () => <Main>
				<UserDetail />
				<Button onClick={signOut} plain type={ButtonTypes.danger} class={styles.mt20}>
					退出登录
				</Button>
			</Main>,
			footer: () => <Footer />
		}
		return () => <Container v-slots={slots} />
	}
})